* {
  text-decoration: none;
  margin: 2px;
  padding: 0;
  list-style: none;
}

.container {
  display: flex;
  height: 100vh;
}
.leftcontainer {
  position: relative;
  flex: 20rem;
  width: 100%;
  height: 100%;
  background: #042331;
  padding: 10px;
}
.rightcontainer {
  position: relative;
  flex: 70%;
  overflow: hidden;
  background: #e6e6fa;
  padding-top: 16px;
  padding: 10px;
}
header {
  text-align: center;
  color: white;
  font-size: 30px;
  margin-bottom: 25px;
}

.btn {
  display: block;
  width: 100%;
  height: 40px;
  margin-top: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  border-bottom: 1px solid black;
}

.rightcontainer .box {
  position: absolute;
  right: -110%;
  border-radius: 0.5rem;
  box-shadow: var(--box-shadow);
  width: 100%;
}
.rightcontainer .box.active {
  right: -0.5rem;
  transition: 0.1s linear;
}

.yrs {
  font-size: 35px;
  margin-bottom: 10px;
  color: #097aaf;
}
.qua {
  font-size: 25px;
  margin-bottom: 10px;
  color: #2e94c4;
}
.per {
  margin-bottom: 18px;
  font-size: 20px;
  color: #48b3e4;
}
.box-it {
  position: absolute;
  right: -110%;
  border-radius: 0.5rem;
  box-shadow: var(--box-shadow);
  width: 100%;
}
.rightcontainer .box-it.active {
  right: -0.5rem;
  transition: 0.1s linear;
}
.it {
  font-size: 35px;
  margin-bottom: 10px;
  color: #097aaf;
}
.its {
  font-size: 18px;
  margin-bottom: 10px;
  color: #2e94c4;
}
.its span {
  font-weight: bold;
}
.box-per {
  position: absolute;
  right: -110%;
  border-radius: 0.5rem;
  box-shadow: var(--box-shadow);
  width: 100%;
}
.rightcontainer .box-per.active {
  right: -0.5rem;
  transition: 0.1s linear;
}
.perone {
  font-size: 18px;
  margin-bottom: 10px;
  color: #097aaf;
  font-weight: bold;
}
.perwo {
  font-size: 13px;
  margin-bottom: 10px;
  color: #3d85a7;
}

@media screen and (max-width: 400px) {
  .leftcontainer {
    position: relative;
    flex: 5rem;
    padding: 5px;
  }
  .rightcontainer {
    position: relative;
    flex: 20rem;
    margin-right: 2px;
  }

  .yrs {
    font-size: 20px;
    margin-bottom: 10px;
  }
  .qua {
    font-size: 15px;
    margin-bottom: 10px;
  }
  .per {
    margin-bottom: 18px;
    font-size: 14px;
  }
  .it {
    font-size: 20px;
    margin-bottom: 10px;
  }
  .its {
    font-size: 15px;
    margin-bottom: 10px;
    line-height: 1.2;
  }
}
